aside.user {
    background: #fff;
    border-radius: 4px;
    box-shadow: 0 1px 3px rgba(0,0,0, .2);
    position: absolute;
    left: -10px;
    top: 65px;
    width: 100%;

    a {
        color: $main-color;
        font-weight: 500;
        padding: 5px 15px;

        &:hover {
            color: $blue-info; } } }



.columns {
    &.has-aside {
        margin-right: -20px; } }

.aside {
    background: $background;

    .tag {
        &.is-uri {
            position: absolute;
            top: 20px;
            right: 5px; } } }

