$thumb-size : 20px;
$thumb-bg-color : #FAFAFA;

$track-width : 34px;
$track-height : 14px;

$focus-ring-size : 42px;
$focus-ring-left : -(($focus-ring-size - $thumb-size) / 2);
$focus-ring-top : -(($focus-ring-size - $thumb-size) / 2);

.ui-switch {
    position: relative;
    &:before {
      content: "";
      display: block;
      margin-top: -10px; }

    &.checked {
        .ui-switch-track {
            background-color: rgba($blue-info, 0.3); }


        .ui-switch-thumb {
            left: 14px;
            background-color: $blue-info; }


        .ui-switch-focus-ring {
            left: $focus-ring-left + 14px;
            background-color: rgba($blue-info, 0.12); } } }




.ui-switch-track {
    position: absolute;
    top: ( ($thumb-size - $track-height) / 2 );

    height: $track-height;
    width: $track-width;

    background-color: rgba(black, 0.1);
    border-radius: 8px;

    transition: background-color 0.1s linear; }


.ui-switch-thumb {
    position: absolute;
    top: 0;
    left: 0;

    width: $thumb-size;
    height: $thumb-size;

    border-radius: 50%;
    background-color: $thumb-bg-color;

    box-shadow: 0 1px 3px rgba(black, 0.4);

    transition: all 0.2s ease; }


.ui-switch-focus-ring {
    position: absolute;
    top: $focus-ring-top;
    left: $focus-ring-left;
    z-index: -1;

    border-radius: 50%;
    width: $focus-ring-size;
    height: $focus-ring-size;

    background-color: rgba(black, 0.1);
    transform: scale(0);
    opacity: 0;

    transition: all 0.2s ease; }


.ui-switch-input {
    position: absolute;
    opacity: 0;

    &:focus ~ .ui-switch-focus-ring {
        transform: scale(1);
        opacity: 1; } }



