.has-dropdown {
    position: relative; }
.dropdown {
    background: #ffffff;
    box-shadow: 0 1px 3px rgba(0,0,0, .2);
    min-width: 250px;
    max-height: 400px;
    font-weight: 600;
    overflow: hidden;
    position: absolute;
    z-index: 1000;

    ul {
        list-style-type: none;
        margin: 0;
        padding: 0; }

    li {
        &:not(:last-child) {
          border-bottom: 1px solid $gray-border; }
        a {
            color: $main-color;
            display: block;
            font-weight: 500;
            padding: 10px; } } }



