.bounce-enter {
  animation: bounce-in .5s; }

.bounce-leave {
  animation: bounce-out .5s; }

@keyframes bounce-in {
  0% {
    transform: scale(0); }

  50% {
    transform: scale(1.5); }

  100% {
    transform: scale(1); } }


@keyframes bounce-out {
  0% {
    // transform: scale(1);
 }    //ight: 25px

  10% {
    right: 100px; }

  100% {
    right: -600px; } }

