.control {
    @include m(with-label) {
        margin-top: 5px;
        label {
            color: $input-label-color;
            font-size: 12px;
            padding-left: 10px;
            position: absolute;
            z-index: 1;
            top: 10px; }

        .input {
            padding-top: 25px;
            position: relative;
            &:focus {
                font-weight: 500;

                + label {
                    color: $blue-info; } }
            &.is-danger {
                border-color: $red;
                + label {
                    color: $primary; } }
            &.is-large {
                height: auto; }
            + label + .tag {
                position: absolute;
                bottom: 10px;
                right: 20px; } }

        .select {
            display: block;
            height: auto;
            width: 100%;

            &:after {
                border: 2px solid $main-color;
                border-right: 0;
                border-top: 0; } }

        select {
            padding-top: 20px;
            width: 100%;

            &:focus {
                font-weight: 500;

                + label {
                    color: $blue-info; } } }
        .auto-height {
            overflow: hidden;
            min-height: 75px; } }

    label {
        &.checkbox {
            color: $main-color;
            padding-left: 25px;
            position: relative;
            font-weight: 500;

            input {
                border: none;
                visibility: hidden; }


            input:before {
                content: "";
                border: 2px solid $main-color;
                border-radius: 4px;
                display: inline-block;
                height: 16px;
                width: 16px;
                left: 15px;
                top: 0;
                position: absolute;
                visibility: visible; }

            input:checked:after {
                content: "";
                display: block;
                border-radius: 2px;
                background: $blue-info;
                height: 8px;
                position: absolute;
                width: 8px;
                left: 19px;
                top: 4px;
                transition: all .4s ease;
                visibility: visible; } }
        &.radio {
            input {
                height: 16px;
                width: 16px;
                margin-right: 10px; }
            input:checked:after {
                content: "";
                display: block;
                border-radius: 50%;
                background: $blue-info;
                height: 8px;
                position: absolute;
                width: 8px;
                left: 2px;
                top: 2px;
                transition: all .4s ease; } } }

    &.--price {
        max-width: 175px; } }

.input {
    &.has-addon {
        padding-left: 20px;
        + label + .addon {
            position: absolute;
            font-weight: 700;
            color: $input-label-color;
            top: 30px;
            left: 10px; }
        &.has-addon-right {
            padding-left: 10px;
            padding-right: 20px;
            + label + .addon {
                left: auto;
                right: 10px; } } } }



.form-actions {
    padding: 40px 20px;
    &.has-background {
        background: $grey-lighter; } }

.help {
    color: $grey-dark;
    font-size: 13px; }
