.card {
    &.is-mini {
        width: 100px; }

    figure {
        padding: 5px;
        text-align: center; } }


.card-footer {
    background: #ffffff;
    border-top: none; }

.card-footer-item {
    color: $menu-link;
    font-weight: 600;
    .mdi {
        margin-right: 5px; }
    &:not(:last-child) {
        border-right: 0; } }
