.sidebar {
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#0b1e35+0,1a4065+74,1a4065+100 */
    background: darken($primary, 10); /* Old browsers */;
    background: -moz-linear-gradient(top,  darken($primary, 10) 0%, $primary 100%); /* FF3.6-15 */;
    background: -webkit-linear-gradient(top,  darken($primary, 10) 0%, $primary 100%); /* Chrome10-25,Safari5.1-6 */;
    background: linear-gradient(to bottom,  darken($primary, 10) 0% , $primary 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */;
    //filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0b1e35', endColorstr='#1a4065',GradientType=0 ); /* IE6-9 */ $primary
    //+box-sizing(border-box)
    // border-right: 1px solid rgba(255,255,255, .05)
    border-right: 1px solid darken($primary, 5);


    @include has(brand) {
        background: $dark;
        border-bottom: 1px solid #102441;
        height: $top-header-height;
        display: flex;
        align-items: center;
        padding-left: 20px;
        box-shadow: 1px 0 0 #18191D; }

    nav {
        padding: 20px 10px;

        li {
            margin-bottom: 10px;
            a {
                border-radius: 4px;
                color: $menu-link;
                display: flex;
                align-items: center;
                line-height: 40px;
                padding: 0 10px;
                transition: all .3s ease;

                svg {
                    margin-right: 10px;
                    transition: all .5s ease; }
                .arrow {
                    margin-left: auto; }
                &:hover {
                    background: #162C52;
                    background: rgba(255,255,255, .04);
                    color: #fff;
                    path {
                        fill: #fff; }
                    .triangle {
                        fill: #fff; } }
                &.active {
                    color: $blue-info;
                    path {
                        fill: $blue-info; }
                    .triangle {
                        fill: $blue-info; } } }
            ul {
                padding: 10px 0 10px 50px;
                li {
                    margin-bottom: 5px;
                    a {
                        line-height: 18px;
                        padding: 0;
                        &:hover {
                            background: transparent;
                            color: #fff; } } } } } } }
