.user-notes {
    .notification {
        margin-bottom: 5px; }
    .notification-meta {
        color: $main-color;
        font-size: 13px;

        margin-bottom: 20px;

        strong {
            color: $main-color; } }

    &.folded {
        max-height: 400px;
        overflow: hidden; } }

.more-notes {
    color: $main-color;
    font-size: 24px;
    display: inline-block;
    margin-top: -10px;
    margin-bottom: 10px; }


