header.header {
    height: $top-header-height; }
.header {
    box-shadow: 0 1px 4px rgba(0,0,0,.1);
    @include has(actions) {
        padding-left: 20px; } }
.header-item {
    a {
        color: $dark;
        display: flex;
        align-items: center;
        text-align: left;

        svg {
            margin-right: 10px; }
        &:hover {
            strong {
                color: $blue-info; }
            path {
                fill: $blue-info; }
            small {
                color: $dark; } } }
    strong {
        font-weight: 500; }
    small {
        display: block;
        color: $menu-link;
        font-size: 12px;
        line-height: 1; } }


.logged-user {
    background: $grey-bg;
    border-left: 1px solid #f6f6f6;
    position: relative;
    text-align: left;

    .avatar {
        background: $background-dark;
        border-radius: 50%;
        color: $blue-info;
        font-size: 16px;
        font-weight: 500;
        width: 35px;
        height: 35px;
        line-height: 35px;
        text-align: center; }
    small {
        font-size: 12px; }

    .inner {
        padding: 0 20px; }
    @include has(name) {
        color: $menu-link; } }
