.hero {
    &.is-primary.is-bold {

        /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#0b1e35+0$blue-info+100 */
        background: #0b1e35; /* Old browsers */;
        background: -moz-linear-gradient(left,  darken($primary, 10) 0%, $primary 100%); /* FF3.6-15 */;
        background: -webkit-linear-gradient(left, darken($primary, 10)  0%, $primary 100%); /* Chrome10-25,Safari5.1-6 */;
        background: linear-gradient(to right,  darken($primary, 10) 0%, $primary 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */;
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0b1e35', endColorstr='$blue-info',GradientType=1 ); /* IE6-9 */;
        color: $main-color;

        h1 {
            color: #ffffff;
            font-size: 24px; } } }
