
$body-background: #fff;
$size-normal: 14px;
$header-height: 50px;
$top-header-height: 60px;
$gray-border: #F4F4F4;


$primary: #153454;
$main-color: #5C747B;

$text: darken($main-color, 10%);

// color for brand background
$dark: #18191D;
// menu link color
$menu-link: lighten($main-color, 10%);

$background-dark: #0B1E35;

$blue-info: #00BAF3;

$grey-bg: #FBFBFB;

// Font defaults
$family-primary: "Roboto", "Helvetica Neue", Arial, sans-serif;

$font-family-heading: "Roboto","Helvetica Neue",sans-serif;

// Form
$input-radius: 6px;
$input-border-color: #F4F4F4;
$input-label-color: darken(#AEB9BD, 10%);
$input-color: $main-color;
$input-focus-border-color: $main-color;
$input-focus-label-color: $blue-info;
