@mixin has($name) {
    @at-root #{&}__#{$name} {
        @content; } }
@mixin m($name) {
    @at-root #{&}--#{$name} {
        @content; } }

// Box sizing
@mixin box-sizing($boxmodel) {
  -webkit-box-sizing: $boxmodel {
     -moz-box-sizing: $boxmodel {
          box-sizing: $boxmodel; } } }


@mixin keyframes($name) {
  @-webkit-keyframes #{$name} {
    @content; }
  @-moz-keyframes #{$name} {
    @content; }
  @-ms-keyframes #{$name} {
    @content; }
  @keyframes #{$name} {
    @content; } }
