.is-menu-item {
    margin-bottom: 20px !important;
    .control {
        margin-bottom: 0 !important; }
    .is-item-url {
        margin-top: -20px !important; } }
.item-actions {

    .mdi {
        color: $grey;
        display: block;
        font-size: 20px;
        text-align: center;
        &:hover {
            color: $primary; }
        &.draggable {
            cursor: move; } } }

.sortable-chosen {
    background: $grey-lighter;
    padding: 0 20px 20px 0; }

.sortable-fallback {
    background: red; }

.sortable.dropped {
    animation: flash-primary 1s;
    padding: 0 20px 20px 0; }

@include keyframes(flash-primary) {
    0% {
        background: $grey-lighter; }
    25% {
        background: $primary; }
    75% {
        background: $grey-lighter; }

    100% {
        background: transparent; } }
