
a {
    transition: all .3s ease; }

.is-relative {
    position: relative; }

[v-cloak] {
    display: none; }

.is-hidden {
    display: none; }

.liner {
    color: $input-label-color;
    font-size: 14px;
    padding: 10px 0 30px 0;
    overflow: hidden;
    position: relative;
    text-align: center;

    span {
        font-weight: 700;
        text-transform: uppercase;
        position: relative;

        &:after,
        &:before {
            content: "";
            position: absolute;
            border-top: 2px solid rgba(0,0,0, .1);
            width: 600px;
            height: 2px;
            top: 50%;
            margin-top: -2px/2px; }

        &:before {
            right: 100%;
            margin-right: 40px; }

        &:after {
            left: 100%;
            margin-left: 40px; } }

    &.--is-right {
        text-align: right;
        span {
            margin-right: 60px; } }


    &.--is-left {
        text-align: left;
        span {
            margin-left: 60px; } } }

.has-padding-bottom {
    padding-bottom: 20px; }
.has-padding-top {
    padding-top: 20px; }
.has-padding-right {
    padding-right: 20px; }
.has-padding-left {
    padding-left: 20px; }

.is-gray {
    color: #aeb1b5; }

.is-bold {
    font-weight: 500; }
