.is-box {
    overflow: hidden;
    padding: 0;

    .columns {
        height: 100%;
        &.no-top {
            margin-top: 0; } } }

.box-sidebar {
    background: #fff;
    padding: 20px 0 20px 10px;
    width: 200px;

    ul {
        list-style: none;
        margin: 0;
        text-align: right;

        a {
            color: $grey-dark;
            display: block;
            font-weight: 500;
            padding: 7px 20px;
            &:hover {
                background: $grey-lighter; }
            &.is-active {
                background: $grey-lighter; } } } }


.box-content {
    background: $grey-lighter;
    flex: 1;
    padding: 20px 20px 40px 20px;

    overflow-y: scroll;
    height: 100%; }
